import React, {Component} from 'react';
import PropTypes from 'prop-types';

import Note from './note';

class octave extends Component {

  constructor(props) {
    super();
  }

  render() {
    return (
      <tr className='octave'>
        <Note level={this.props.level} note='C' type='standard' keyOffset={1}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='C' type='sharp'    keyOffset={2}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='D' type='standard' keyOffset={3}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='D' type='sharp'    keyOffset={4}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='E' type='standard' keyOffset={5}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='F' type='standard' keyOffset={6}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='F' type='sharp'    keyOffset={7}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='G' type='standard' keyOffset={8}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='G' type='sharp'    keyOffset={9}   selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='A' type='standard' keyOffset={10}  selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='A' type='sharp'    keyOffset={11}  selectedKeyOffsets={this.props.selectedKeyOffsets} />
        <Note level={this.props.level} note='B' type='standard' keyOffset={12}  selectedKeyOffsets={this.props.selectedKeyOffsets} />
      </tr>
    );
  }
}

octave.propTypes = {
  level: PropTypes.string,
  selectedKeyOffsets: PropTypes.any
};

export default octave;