import React, {Component} from 'react';
import PropTypes from 'prop-types';

const cMajor=[1,3,5,6,8,10,12];

class KeySelector extends Component {

  constructor(props) {
    super();

    this.handleSelectChanged = this.handleSelectChanged.bind(this,false);
  }

  mapKey(shift) {
    return cMajor.map( val => ((val-1+shift)%12)+1);
  }

  handleSelectChanged(evt) {
    console.log('change occurred');

    var input = document.getElementById('key-selector');
    var val = input.value;

    let [offset, keyName] = val.split(':');

    var offsetInt = 0;
    try {
      offsetInt = parseInt(offset);
    } catch(err){}

    var newKeys = this.mapKey(offsetInt);
    console.log(newKeys);

    this.props.onKeyChange(newKeys, keyName, evt);
  }

  render() {
    return (
      <form className='KeySelector'>
        <span>Which key should we select?</span>
        <select id='key-selector' onChange={this.handleSelectChanged}>
          <option value='0:c-std'>C Major</option>
          <option value='1:c-major'>C# Major</option>
          <option value='2:d-std'>D Major</option>
          <option value='3:d-major'>D# Major</option>
          <option value='4:e-std'>E Major</option>
          <option value='5:f-std'>F Major</option>
          <option value='6:f-major'>F# Major</option>
          <option value='7:g-std'>G Major</option>
          <option value='8:g-major'>G# Major</option>
          <option value='9:a-std'>A Major</option>
          <option value='10:a-major'>A# Major</option>
          <option value='11:b-std'>B Major</option>
        </select>
      </form>
    );
  }
}

KeySelector.propTypes = {
  onKeyChange: PropTypes.any
};

export default KeySelector