import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Sound from 'react-sound';
import './note.css';

const path = 'lq/';
const fileDelimiter = '_';
const extension = 'lo.mp3';

class Note extends Component {

  constructor(props) {
    super();

    var isSharp = this.determineIfSharp(props.type);

    this.state = {
      isDown: false,
      audioFile: this.calculateAudioFile(props.level, props.note, props.type, isSharp),
      noteLabel: this.calculateNoteLabel(props.level, props.note, props.type, props.keyOffset, isSharp),
      cssClasses: this.calculateCssClasses(props.note, props.keyOffset, props.selectedOffsets, isSharp)
    };
    
    //this.onClick = this.onClick.bind(this, true);

    // console.log('props for Note');
  }

  determineIfSharp(type) {
    return type === 'sharp';
  }

  /**
   * Determines the path of the audio file for the note
   * @param {integer} level - the octave level
   * @param {string} note - [C|D|E|F|G|A|B]
   * @param {string} type - (sharp|normal)
   * @param {boolean} isSharp - whether the note is sharp or not
   */
  calculateAudioFile(level, note, type, isSharp) {
    return (path + level + fileDelimiter +
      note + fileDelimiter +
      (isSharp ? 'sharp' : '_____') + fileDelimiter +
      extension);
  }

  /**
   * Determines the label for the note
   * @param {integer} level - the octave level
   * @param {string} note - [C|D|E|F|G|A|B]
   * @param {string} type - (sharp|normal)
   * @param {boolean} isSharp - whether the note is sharp or not
   */
  calculateNoteLabel(level, note, type, keyOffset, isSharp) {
    return `${level} - ${note}${(isSharp?'#':'')}`;
  }

  calculateCssClasses(note, keyOffset, selectedOffsets, isSharp) {
    return (`note offset-${keyOffset} note-${note?note.toLowerCase():''}${(isSharp?'-sharp':'')}`);
  }

  handleFinishedPlaying() {
    this.setState(prevState => {
      return({ isDown: false});
    });
  }

  toggleIsDown() {
    this.setState(prevState => {
      return({ isDown: !prevState.isDown })
    });
  }

  /*
  setIsDown(isDown) {
    this.setState(prevState => ({ isDown: isDown }));
  }
  */

  render() {

    let isNoteSelected = false;
    if (this.props.selectedKeyOffsets) {
      isNoteSelected = this.props.selectedKeyOffsets.includes(this.props.keyOffset);
    }

    return (
      <td className={this.state.cssClasses + ' ' + (this.state.isDown?'playing':'') + ' ' + (isNoteSelected?'selected':'')}
        onClick={e => this.toggleIsDown(true, e)}
      >
        <span title={this.audioFile}>
          {this.state.noteLabel}
        </span>
        <Sound url={this.state.audioFile} playStatus={this.state.isDown ? 'PLAYING' : 'STOPPED'}
          onFinishedPlaying={e => this.handleFinishedPlaying(e)}
        />
      </td>
    );
  }
}

Note.propTypes = {
  level: PropTypes.string,
  type: PropTypes.string,
  note: PropTypes.string,
  keyOffset: PropTypes.number,
  selectedKeyOffsets: PropTypes.any
};

export default Note